.ishop-app {
  overflow: hidden;
  width: 100%;
  background-color: #FAFAFA;
  min-height: 1700px;
}

.main-row {
  padding: 30px 8%;
}
.bestsellers {
  margin: 10px 8% 15px;
  background-color: #fff;
  max-width: 100%;
  padding: 10px 25px;
  border-radius: 8px;
}
.bestsellers .text {
  display: inline-block;
  font-family: 'Roboto', sans-serif;
  font-size: 17px;
  font-weight: 600;
  margin-right: 15px;
}
.bestsellers img {
  width: 20px;
  height: 20px;
  margin-bottom: 5px;
}
.bestsellers {
  box-shadow: 0px 1px 3px 0px rgba(0,0,0,0.2), 0px 1px 1px 0px rgba(0,0,0,0.14), 0px 2px 1px -1px rgba(0,0,0,0.12);
}
.leaders-list {
  margin: 10px 8%;
  max-width: 100%;
  height: 400px;
  display: flex;
  justify-content: space-between;
  margin-bottom: 50px;
}

button {
  outline: none;
}
button:active {
  outline: none;
}
button:focus {
  outline: none;
}
input {
  outline: none;
}
input:active {
  outline: none;
}
input:focus {
  outline: none
}
