.item-wrapper {
  overflow: hidden;
  width: 100%;
  background-color: #FAFAFA;
  height: 90vh;
  padding: 20px 8%;
  font-family: 'Varela Round', sans-serif;
  font-family: 'Roboto', sans-serif;
}
.selected-cat {
  background-color: #efefef;
  max-width: 100%;
  padding: 10px 10px;
  border-radius: 5px;
  margin-bottom: 30px;
}
.selected-cat a {
  color: black;
  display: inline-block;
  font-size: 15px;
  font-weight: 500;
}
.selected-cat a:hover {
  color: #333;
}
.img-item-wrapper {
  max-width: 100%;
  height: 400px;
  transition: all .8s ease;
}
.img-item-wrapper:hover {
  transform: scale(1.1);
}
.img-item-wrapper img {
  width: auto;
  max-width: 100%;
  max-height: 100%;
  height: auto;
  display: block;
  margin: 0 auto;
}
.inner-wrapper {
  width: 100%;
  background-color: #fff;
  padding: 10px 30px 20px;
  border-radius: 10px;
}
ul.breadcrumb {
  padding: 0;
  list-style: none;
  margin-bottom: 0;
  background-color: #EFEFEF;
  padding-left: 20px;
}
ul.breadcrumb li {
  display: inline-block;
}
ul.breadcrumb li+li:before {
  padding: 5px;
  color: black;
  content: "/\00a0";
}
ul.breadcrumb li a:hover {
  color: #333;
  text-decoration: underline;
}
.item-code {
  position: relative;
  margin-top: -22px;
  margin-right: 10px;
  color: #555;
  float: right;
  font-size: 15px;
}
.price-product {
  font-size: 20px;
  font-weight: 600;
  margin-top: 20px;
  margin-bottom: 20px;
}
.divider {
  display: block;
  height: 1px;
  width: 50%;
  background-color: #ccc;
}
.forms {
  margin-top: 10px;
}

.forms span {
  font-size: 16px;
  display: block;
  margin-top: 12px;
}
.forms input {
  display: block;
  border-radius: 20px;
  padding: 5px 15px;
  margin-top: 5px;
  margin-bottom: 2px;
  font-size: 15px;
  border: solid 2px #ccc;
}
.forms input:focus {
  border: solid 2px orange;
  border-bottom: solid 2px orange;
  padding: 6px 12px;
  margin-top: 3px;
  margin-bottom: 0;
}
.forms button {
  margin-top: 15px;
  padding: 5px 30px;
  font-size: 16px;
  display: inline-block;
  float: left;
  background-color: #13BA34;
  color: #fff;
  transition: all .2s ease;
}
.forms button:hover {
  background-color: #0E8726;
  color: #fff;
}
.order-block {
  margin-top: 5px;
}

.order-block .isExist {
  margin-top: 10px;
  margin-left: 20px;
  color: #13BA34;
}
.order-block .isExist img {
  margin-bottom: 4px;
  margin-right: 0;
}
.order-block .isExist span {
  margin-left: 5px;
  display: inline-block;
}
