.sidebar {
  background-color: #fff;
  width: 100%;
  border-radius: 8px;
  padding: 25px;
  border: solid 1px #ddd;
}
.side-box {
  border-bottom: solid 1px #ccc;
  margin-bottom: 20px;
  padding-bottom: 10px;
}
.sidebar p {
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
  font-weight: 500;
}
.form {
  margin: 0 auto;
  padding: 30px 10px 30px;
}
.input-range__track--active {
  background: orange !important;
}
.input-range__slider {
  background: orange !important;
  border: 1px solid orange !important;
}
.roww {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}
.half {
  align-items: center;
  display: flex;
  width: 47%;
}
.half span {
  padding-right: 10px;
}
.el-input {
  display: inline-block;
  font-size: 14px;
  position: relative;
  width: 100%;
}
.el-input-inner {
  color: #484748;
  font-size: 16px;
  padding: 16px 11px;
  border: 1px solid #e1e1e1;
  border-radius: 4px;
  box-shadow: inset 0 2px 1px 0 rgba(0,0,0,.1);
  display: inline-block;
  height: 30px;
  width: 100%;
}
.MuiIconButton-root-112 {
  padding: 0 !important;
}
.MuiTypography-body1-59 {
  font-size: 15px !important;
}
label {
  margin-bottom: 0;
}

input[type='number'] {
    -moz-appearance: textfield;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}

.unique-button {
  font-size: 12px;
  padding: 3px 10px;
  background-color: white;
  border: solid 1px #43B02A;
  border-radius: 20px;
  color: #43B02A;
  transition: all .3s ease;
}
.unique-button:hover {
  background-color: #43B02A;
  color: white;
}
.side-box .unique-button {
  float: right;
  margin-right: 3px;
}
.side-box .form {
  margin-top: 35px;
}

.maxPrice {
  font-size: 10px;
  padding: 1px 5px;
  background-color: white;
  border: solid 1px black;
  border-radius: 20px;
  display: inline-block;
  color: black;
}
.checkbox-item {
  padding: 0;
}
