.carousel {
  width: 100%;
  height: 420px;
  background-color: #fff;
  overflow: hidden;
}
.car-item {
  background-color: red;
}
.carousel .slide {
  padding: 0 !important;
}
.carousel .control-dots .dot.selected, .carousel .control-dots .dot:hover {
  padding: 0 !important;
}
.carousel .control-dots .dot:focus {
  outline: none !important;
}
