.card {
  background-color: #fff;
  border-radius: 10px;
  height: 400px;
  width: 231px;
  padding: 20px;
  border: solid 1px #ddd;
}
.img-wrapper {
  max-width: 100%;
  height: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.img-wrapper img {
  width: auto;
  height: auto;
  max-width: 100%;
  max-height: 100%;
}
.card-body {
  font-family: 'Roboto', sans-serif;
  padding: 5px 5px 8px;
  border-bottom: solid 1px #ddd;
  flex: none;
  height: 122px;
}
.card-body a {
  font-size: 15px;
  text-decoration: none;
  color: black;
  font-weight: 500;
}
.card-body:hover a {
    text-decoration: underline;
    text-decoration-color: black;
}
.card-body .price {
  font-size: 17px;
  font-weight: 500;
  position: absolute;
  bottom: 65px;
}
.card-info {
  margin-top: 14px;
  display: inline-block;
}
.isExist {
  font-size: 15px;
  float: left;
  color: #43B02A;
  margin-top: -2px;
}
.isExist img {
  margin-bottom: 5px;
}
.isExist span {
  margin-left: 5px;
  display: inline-block;
}
.code {
  float: right;
  font-size: 15px;
}
.fav-check {
  width: 18px;
  height: 18px;
  display: block;
  background: url('./img/favs.png') no-repeat;
  background-position: 0 0;
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
}
.fav-check:hover {
  background-position: -18px 0;
}
.fav-selected {
  background-position: -18px 0;
}
