.shop-page {
  width: 100%;
  background-color: #FAFAFA;
  min-height: 1600px;
}
.main-row {
  padding: 30px 8%;
  max-width: 100%;
}
.selected {
  margin: 0 5px;
  background-color: #fff;
  max-width: 100%;
  padding: 10px 25px;
  border-radius: 8px;
  box-shadow: 0px 1px 3px 0px rgba(0,0,0,0.2), 0px 1px 1px 0px rgba(0,0,0,0.14), 0px 2px 1px -1px rgba(0,0,0,0.12);
}
.selected span {
  display: inline-block;
  font-family: 'Roboto', sans-serif;
  font-size: 17px;
  font-weight: 500;
  margin-right: 5px;
}
.leaders-list-2 {
  margin: 12px 0;
  width: 100%;
  height: 400px;
  display: flex;
  margin-bottom: 30px;
  flex-wrap: wrap;
  align-items: center;
}
.leaders-list-2 .card {
  margin-left: 5px;
  margin-right: 5px;
  margin-bottom: 20px;
}
.leaders-list-2 .card-body a {
  font-size: 15px;
}
.shop-items {
  display: inline-block;
}
