.preheader {
  height: 20px;
  width: 100%;
  background-color: #fff;
}
header {
  width: 100%;
  height: 64px;
  background-color: #43B02A;
  display: flex;
  position: relative;
  align-items: center;
  padding: 0 8%;
}
/*
main color : #43B02B
darker : #379425
brighter : #3BA723
*/


.go2home {
  font-size: 20px;
  font-weight: 600;
  margin-right: 15%;
  margin-left: 3%;
  width: 90px;
}

.ul-header {
  margin-right: 15px;
  margin-left: 10%;
}
.ul-header li {
  list-style: none;
  display: inline-block;
  padding: 15px 10px 0;
}
.ul-header li a {
  color: white;
}
.ul-header li a:hover {
  text-decoration: none;
}
.ul-header li a:hover::after {
  content: '';
  width: 100%;
  height: 2px;
  background-color: #fff;
  margin-top: 3px;
  margin-bottom: -5px;
  display: block;
}
.title-link {
  color: white;
}
.title-link:hover {
  text-decoration: none;
  color: white;
}
.tocart {
  position: absolute;
  right: 11%;
  cursor: pointer;
}

.search-block {
  z-index: 10;
  position: absolute;
  margin: 0 25%;
}
.search-ul {
  padding: 0;
  max-height: 375px;
  overflow: hidden;
  background-color: #fefefe;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  box-shadow: 0px 1px 3px 0px rgba(0,0,0,0.2), 0px 1px 1px 0px rgba(0,0,0,0.14), 0px 2px 1px -1px rgba(0,0,0,0.12);
}
.search-li {
  list-style-type: none;
  display: flex;
  padding: 10px 15px 5px;
  background-color: #fefefe;
  width: 500px;
}
.preview-img {
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.preview-img img {
  width: auto;
  max-width: 100%;
  height: auto;
  max-height: 100%;
}
.preview-body {
  display: inline-block;
  position: relative;
  padding-left: 15px;
  display: flex;
}
.preview-text {
  width: 300px;
  padding-right: 5px;
}
.preview-text a {
  font-size: 15px;
  color: black;
}
.preview-price {
  margin-top: 10px;
  margin-left: 5px;
}
.cart-window {
  width: 45%;
  position: absolute;
  display: none;
  background-color: #76BD66;
  right: 10%;
  margin-top: 0;
  z-index: 20;
  padding: 10px;
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
}
.cart-window .close {
  background: url('./img/close.png') no-repeat;
  display: inline-block;
  width: 24px;
  height: 24px;
  cursor: pointer;
  position: absolute;
  top: 10px;
  right: 10px;
}
.cart-window .close:hover {
  background: url('./img/close-hover.png') no-repeat;
}
.cart-window h5 {
  text-align: center;
  margin-bottom: 40px;
  margin-top: 10px;
}
.cart-window .total {
  position: relative;
  margin: 20px 10px 10px 10px;
  display: flex;
  justify-content: flex-end;
}
.total .unique-button {
  background-color: #43B02A;
  color: white;
  font-size: 16px;
  width: 100px;
  margin-left: 10px;
  transition: all .2s ease;
}
.total .unique-button:hover {
  background-color: #2F7D1E;
  border: solid 1px #2F7D1E;
}

.cart-item {
  list-style-type: none;
  display: flex;
  padding: 10px 15px;
  background-color: #fff;
  border-radius: 8px;
  margin-bottom: 5px;
}
.cart-item .close-item {
  background: url('./img/close-item.png') no-repeat;
  display: inline-block;
  width: 16px;
  height: 16px;
  cursor: pointer;
  position: absolute;
  right: 20px;
  margin-top: 0;
}
.cart-item .close-item:hover {
  background: url('./img/close-item-hover.png') no-repeat;
}
.items-counter {
  position: absolute;
  right: 70px;
  margin-top: 12px;
}
.items-counter span {
  vertical-align: middle;
}
.counter-minus {
  background: url('./img/minus.png') no-repeat;
  display: inline-block;
  width: 24px;
  height: 24px;
  cursor: pointer;
  margin-right: 10px;
}
.counter-plus {
  background: url('./img/plus.png') no-repeat;
  display: inline-block;
  width: 24px;
  height: 24px;
  cursor: pointer;
  margin-left: 10px;
}
