.add-item {
  width: 15%;
  margin: 10px auto 30px;
}
.add-item span {
  font-size: 16px;
  display: block;
  margin-top: 10px;
}
.add-item input {
  display: block;
  border-radius: 20px;
  padding: 5px 15px;
  margin-top: 5px;
  margin-bottom: 2px;
  font-size: 15px;
  border: solid 2px #ccc;
  width: 100%;
}
.add-item input:focus {
  border: solid 2px orange;
  border-bottom: solid 2px orange;
  padding: 6px 12px;
  margin-top: 3px;
  margin-bottom: 0;
}
.add-item button {
  margin-top: 20px;
  padding: 7px 30px;
  font-size: 16px;
  border-radius: 20px;
  border: none;
  width: 100%;
  background-color: orange;
  display: inline-block;
  cursor: pointer;
  transition: all .3s ease;
  color: black;
}
.add-item button:hover {
  color: white;
}

.display-item {
  max-width: 100%;
  margin: 0 8%;
}
.display-item-card {
  display: inline-block;
  margin: 0 5px;
}
.display-item button {
  margin: 0;
  display: block;
}
